



























































import goods from 'template/goods'
import loadMore from 'template/load_more.vue'
import naData from 'template/no_data'
import loading from 'template/loading'
import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	components: {
		goods,
		loadMore,
		naData,
		loading
	},
	computed: mapState(['globalData']),
	data() {
		return {
			HotsaleGoods: [],
			showHot: true,
			page_num: 1,
			page_size: 5,
			list: [],
			nodata: false,
			nodataText: "当前没有相关商品~",
			loadingType: 0,
			field: 0,
			keywords: '',
			isLoading: false,
			backTopValue: false,
			isSearching: false,
			searchCache: [],
			isVip: false
		};
	},
	// 监听滚动条坐标
	onPageScroll: function(e) {
		var scrollTop = e.scrollTop
		var backTopValue = scrollTop > 400 ? true : false
		this.backTopValue = backTopValue

	},

	onPullDownRefresh() {
		console.log(this.keywords)
		if (this.keywords != '') {
			this.loadingType = 0
			setTimeout(() => {
				this.list = []
				this.page_num = 1
				this.getList()
				uni.stopPullDownRefresh()
			}, 800);
		}
	},
	onReachBottom() {
		if (this.keywords != '') {
			if (this.loadingType !== 0) {
				return;
			}
			this.loadingType = 1;
			var page_num = this.page_num
			this.page_num = this.page_num + 1
			console.log(this.page_num)
			this.getList()
		}

	},
	onLoad() {
		this.getCache()
		if (this.$store.state.globalData.userInfo.is_member) {
			this.isVip = true
			this.getHotsaleGoods()
		}
	},
	methods: {
		showLoading() {
			this.isLoading = true
			setTimeout(() => {
				this.isLoading = false
			}, 800);
		},
		//获取搜索历史
		getCache() {
			var search = uni.getStorageSync('searchCache')
			if (search) {
				this.searchCache = search
			} else {
				this.searchCache = []
			}
		},
		//搜索记录存入缓存
		putCache(keyword) {
			var search = this.searchCache
			search.unshift(keyword)
			var temp = []; //一个新的临时数组去重
			for (var i = 0; i < search.length; i++) {
				if (temp.indexOf(search[i]) == -1) {
					temp.push(search[i]);
				}
			}
			uni.setStorage({
				key: "searchCache",
				data: temp
			})
		},

		//清除搜索记录
		removeSearchCache() {
			uni.removeStorageSync('searchCache')
			this.getCache()
		},

		//是否显示搜索记录与热门
		isSearch() {
			this.isSearching = false
			this.nodata = false
			this.list = []
			this.getCache()
		},

		//获取热门搜索
		getHotsaleGoods() {
			var that = this
			
			this.$api.get('/api/index/getHotSearch', {}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					that.HotsaleGoods = res.data.data
				}
			})
		},

		//获取关键词
		getSearch(e) {
			console.log(e)
			this.keywords = e.target ? e.target.value : e
			this.showLoading()
			// this.list = []
			this.getList()
			this.nodata = false
			this.isSearching = true
		},

		//获取搜索结果
		getList() {
			var that = this
			this.nodata = false
			this.showHot = false
			this.putCache(this.keywords) //写入缓存
			this.$api.get('/api/index/searchGoods', {
				keywords: this.keywords,
				page_size: 8,
				page_num: 1
			}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					var oldList = that.list
					var list = res.data.data
					setTimeout(function(res) {
						if (oldList.length == 0) {
							that.loadingType = 0;
							if (list.length <= 0) {
								that.nodata = true
							} else {
								that.list = list
							}
						} else {
							if (list.length <= 0) {
								that.loadingType = 2;
							} else {
								that.loadingType = 0;
								that.list = oldList.concat(list)
							}
						}
					}, 800)
				}
			})
		}
	}
}
